export default function ContactForm() {
  const [open, setOpen] = React.useState(false)
  const [clearSelectFields, setClearSelectFields] = React.useState(false) //1

  const [state, setState] = useState({
    name: "",
    email: "",
    company: "",
    help: "",
    budget: "",
    message: "",
  })

  const [budgetDisable, setbudgetDisable] = useState(true)

  const onChangeHandler = e => {
    const value = e.target.value
    if (e.target.name === "help") {
      const value = helpOptions.filter(item => item.name === e.target.value)
      setbudgetDisable(value[0].budget)
    }
    setState({
      ...state,
      [e.target.name]: value,
    })
  }

  function handleSubmit(e) {
    e.preventDefault()
    if (state.name.length > 25) return
    setOpen(true)
    const form = document.forms["contact"]
    // const formData = new FormData(form)
    // var data = {
    //   "properties":{
    //       "firstname":formData.get("name"),
    //       "email": formData.get("email"),
    //       "company":formData.get("company"),
    //       "help":formData.get("help"),
    //       "budget":formData.get("budget"),
    //       "message":formData.get("message"),
    //     },
    // }
    //   fetch(
    //   "http://localhost:3000/api/contact-form",
    //   { method: "POST", body: JSON.stringify(data),
    //   headers: {
    //     'Content-Type': 'application/json'
    //   }, }
    // )
    //   .then(() => {
    //     // navigate("/thank-you")
    //   })
    //   .catch(error => console.error("Error!", error.message))
    fetch(
      "https://script.google.com/macros/s/AKfycbyVDyCG6u76JONl627-Z6vDPNXB8eFSLjlabDx4gUI4Qf_lSGZL40869BC2r50BiX3BVg/exec",
      { method: "POST", body: new FormData(form) }
    )
      .then(() => {
        navigate("/thank-you")
      })
      .catch(error => console.error("Error!", error.message))
  }

  const errorText = <span style={{ color: "red" }}>Enter a valid name</span>

  const clearButton = () => {
    const form = document.forms["contact"]
    form.reset()
    setClearSelectFields(true)
  }

  return (
    <>
      <Loader open={open} />
      <form name="contact" onSubmit={handleSubmit}>
        <Grid container columnSpacing={5} rowSpacing={6}>
          <Grid item md={6} sm={12} xs={12}>
            <CssTextField
              onChange={onChangeHandler}
              id="standard-basic"
              label="Name"
              variant="standard"
              name="name"
              fullWidth
              required
              helperText={state.name.length > 25 && errorText}
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12}>
            <CssTextField
              onChange={onChangeHandler}
              id="standard-basic"
              label="Email"
              variant="standard"
              name="email"
              fullWidth
              required
              type="email"
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12}>
            <CssTextField
              onChange={onChangeHandler}
              id="standard-basic"
              label="Company"
              variant="standard"
              name="company"
              fullWidth
            />
          </Grid>

          <Grid item md={6} sm={12} xs={12} className="form-fields">
            <Select
              options={helpOptions}
              label="How can we help you?"
              name="help"
              onChange={onChangeHandler}
              clearSelectFields={clearSelectFields}
              resetSelectClearState={() => setClearSelectFields(false)}
            />
          </Grid>
          <Grid item md={6} sm={12} xs={12} className="form-fields">
            <Select
              options={budgetOptions}
              label="Budget"
              name="budget"
              disabled={budgetDisable}
              onChange={onChangeHandler}
              clearSelectFields={clearSelectFields}
              resetSelectClearState={() => setClearSelectFields(false)}
            />
          </Grid>
          <Grid item md={12} xs={12} className="form-fields">
            <textarea
              onChange={onChangeHandler}
              name="message"
              id=""
              cols="30"
              rows="4"
              placeholder="Message"
            ></textarea>
          </Grid>
        </Grid>
        <Grid className="text-right  pe-0 pt-5">
          <Button
            text="CANCEL"
            variant="fill"
            border="#2f2f2f"
            bgClr="#ffffff"
            clr="#2f2f2f"
            className="me-3"
            onClick={clearButton} //3
            type="button"
          />
          <Button
            text="SEND MESSAGE"
            variant="fill"
            bgClr="#f23b48"
            type="submit"
          />
        </Grid>
      </form>
    </>
  )
}
